


*{
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}

ul .active{
    border-bottom: 5px solid red;
    transition: all .5s; 
}
ul .left{
  float: left;
  font-size:40px;
  padding: 20px 10px;
  height: 60px;
  line-height: 10px;
  color: rgb(190, 143, 55);
  text-decoration: none;
  border: none;
}
ul{
  width: 100%;
  height: 50px;
  background-color:rgb(161, 207, 207);
  text-align: center;
  list-style-type: none;
  
  
}
Ul .list{
  text-decoration: none;
  float: right;
  margin: 0px 30px;
  font-size: 30px;
  padding: 20px 10px;
  height: 50px;
  line-height: 10px;
  color: white;

  
}
Ul .list:hover{
  background-color: rgb(131, 187, 201);
}
.contact{
  width: 100%;
  height: 100vh;
  position: relative;

}
ul span{
  font-size: 40px;
  display: none;
}
form{
  display: block;
  background-color: white;
  position: absolute;
  left: 520px;
  
}
.contact  h1{
  text-align: center;
  height: 30px;
  margin: 50px auto;
}

input, textarea{
  display: block;
  width: 500px;
  height: 30px;
  border-radius: 5px;
  margin: 10px 0 ;

}
textarea{
  height: 70px;
}

button{
  padding: 0px 15px;
  margin-top: 20px;
  float: left;
  width: 100px;
  height: 30px;
  background-color: rgb(83, 155, 214);
  color: white;

}
.content{
  position: relative ;
  width: 98%;
  display: inline-block;
}

.content img{
  float: right;
  transform: scale(.7, .7);
  animation: image 5s infinite;
  transition:all 7S;
}
@keyframes image{
  0%{transform: scale(.7, .7)}
  50%{transform: scale(.9,.9)}
  100%{transform: scale(.7,.7)}
}
.content h1{
  position: absolute;
  font-size: 25px;
  top: 300px;
  left: 30px;
  font-weight: bold;
}
.content>p{
  position: absolute;
  font-size: 15px;
  top: 370px;
  left: 30px;
  font-weight: bold;

}
.content h1::after{
  content: "Develop Websites";
  color: chartreuse;
  display: block;

}
.content .link{
  position: relative;
  top: 410px;
  left: 30px;
  background-color:white;
  font-size: 15px;
  padding: 10px 20px;
  text-decoration: none;
  border-radius: 40px;
  border: 2px solid blue;
  outline: none;
  transition: all 0.7s;

}
.link:hover{
  background-color: rgb(117, 224, 210);
}
.services{
  width: 100%;
  height: 100vh;
  position: relative;
}
.services h1{
  text-align: center;
  font-size: 30px;
  margin: 30px auto;
}
.card{
  display: inline-block;
  width: 400px;
  height: 400px;
  margin: 100px 50px ;
  background-color:white;
  border: 1px solid gray;
  border-radius: 5px;
  box-shadow: 2px 2px 5px gray;
}
.card> h1{
  font-size: 30px;
  margin-left: 10px;
}
.card> button{
  width: 120px;
  height: 40px;
  background-color: white;
  color:royalblue;
  font-weight: bold;
  border-radius: 5px;
  border: 2px solid royalblue;
  margin-left: 10px;
  outline: cadetblue;
  margin: 0 0 0 10px;
  transition: all 0.5s;
}
.card> button:hover{
  background-color: rgb(119, 216, 195);
}
footer{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: red;
  color: white;
  text-align: center;
}
@media only screen and (max-width:400px){
  ul{
    max-width: 100%;;
  }
  ul .left{
      font-size:30px;
      height: 10px;
      line-height: 10px;
      

    }
    Ul .list{
      text-align: center;
      display: none;
      text-decoration: none;
      float: right;
      margin: 0 0 0 41px;
      font-size: 10px;
      padding: 10px 10px;
      height: 10px;
      line-height: 5px;
      color:black;

    }
    
    
    .card{
      display: inline-flex;
      flex-direction: column;
      width: 165px;
      height: 230px;
      margin: 30px 90px ;
    }
    .card> h1{
      font-size: 15px;
      margin-left: 10px;
    }
    
    form{
      left:0px;
      width: 100%;  
    }

    input, textarea{
      width: 280px;
    }
    .content{
      width: 100%;
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
    }
    
    .content img{
      transform: scale(.2, .2);
      transition:all 7S;
    }
    @keyframes image{
      0%{transform: scale(.2, .2)}
      50%{transform: scale(.3,.3)}
      100%{transform: scale(.2,.2)}
    }
    .content h1{
      position: absolute;
      font-size: 18px;
      left: 90px;
      top: 400px;
    }
    .content>p{
      position: absolute;
      font-size: 15px;
      top: 450px;
    
    }
    .content .link{
      position: absolute;
      font-size: 10px;
      padding: 5px 10px;
      top: 490px;
      left: 140px;
      
    
    }
    footer{
      text-align: center;
      margin-top: 50vmin ;
      margin-left: 0%;
    }
    ul span{
      display: block;
    }
    
}